// ExpertPage.js
import React from "react";
import "../Style/Expert1.css";

const ExpertPage = () => {
  return (
    <div className="container">
      <div className="image-container">
        <img src="Banner-1.jpg" alt="Expert Image" />
      </div>
      <div className="text-container">
        <p>Expert Skills We Have</p>
        <h1>
          We Have the Skills and Expertise to Deliver High-Quality Custom
          Software
        </h1>
        <div className="row">
          <div className="image-box">
            <img src="Banner-2.jpg" alt="Skill Image" />
          </div>
          <div className="text-box">
            <p>
              "Specializing in cutting-edge technology, our team excels in
              full-stack development, artificial intelligence, and machine
              learning. With a focus on cloud computing, cybersecurity, and IoT,
              we provide tailored software solutions, including custom
              development, data analytics, and sustainable services. Applying
              agile methodologies, prioritizing UX design, and ensuring quality
              engineering, we deliver enterprise solutions and strategic
              consulting. Grounded in innovation, research, and a commitment to
              excellence, we navigate the dynamic tech landscape with
              expertise."
            </p>
            <p>Ai and Machine Learning ,Cloud Computing</p>
            <div className="graph" style={{ width: "95%" }}></div>
            <p>CyberSecurity,Data Analysis</p>
            <div className="graph" style={{ width: "94%" }}></div>
            <button className="quote-button">Get a Quote</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExpertPage;
