// Navbar.js
import React from "react";
import { Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "../Style/Navbar.css"; // Import your external CSS file

const Navbar = () => {
  const handleNavbarTextClick = () => {
    alert("Navbar text clicked!");
  };

  const handleButtonClick = () => {
    alert("Button clicked!");
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-fixed-top">
      <div className="container-fluid">
        <Link className="navbar-brand" to="/">
          <img src={process.env.PUBLIC_URL + "/Aditya.png"} height="40" />
        </Link>
        <div
          className=" pixonate
       "
        >
          <p className="big">Pixonate</p>
          <p className="small">Lab Private Limited</p>
        </div>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon" />
        </button>
        <div
          className="collapse navbar-collapse justify-content-center"
          id="navbarNav"
        >
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-link" to="/">
                HOMEPAGE
              </Link>
            </li>

            <li className="nav-item">
              <Link className="nav-link" to="/about">
                ABOUT
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/Services">
                SERVICES
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/prizingPlan">
                PRIZING PLAN
              </Link>
            </li>

            <li className="nav-item">
              <Link className="nav-link" to="/pages">
                PAGES
              </Link>
            </li>
          </ul>
        </div>
        <div
          className="navbar-text contact-info"
          onClick={handleNavbarTextClick}
        >
          <span className="contact-number">+919922791303</span>
        </div>
        {/* <button
          className="btn btn-outline-primary get-quote-btn get-quote-btn-custom" // Add the custom class here
          onClick={handleButtonClick}
        >
          Get a Quote
        </button> */}
      </div>
    </nav>
  );
};

export default Navbar;
