// ClientFeedback.js
import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Carousel } from "react-bootstrap";
import "../Style/Client1.css";

const ClientFeedback = () => {
  const feedbackItems = [
    {
        id: 1,
        image: "Banner-1.jpg",
        feedbackerImage: "Banner-2.jpg",
        stars: "",
        feedbackText: "Pixonate Lab exceeds expectations! Outstanding tech expertise and prompt delivery.",
        feedbackerName: "Mr.Pradip kate",
        businessOwner: "Startup Newjoiner",
      },

      {
        id: 2,
        image: "Banner-2.jpg",
        feedbackerImage: "Banner-3.jpg",
        stars: "",
        feedbackText: "Impressed with Pixonate's innovative solutions and commitment to quality.",
        feedbackerName: "Mr.Rhohit patil",
        businessOwner: "Business Professional",
      },

      {
        id: 3,
        image: "Banner-3.jpg",
        feedbackerImage: "Banner-4.jpg",
        stars: "",
        feedbackText: "Exceptional service, reliable team. Pixonate Lab stands out in the industry.",
        feedbackerName: "Mr.Harshal Patil",
        businessOwner: "Business Man",
      },

    {
      id: 4,
      image: "Banner-4.jpg",
      feedbackerImage: "Banner-5.jpg",
      stars: "",
      feedbackText: "Reliable partner for software development. Pixonate Lab's professionalism is commendable.",
      feedbackerName: "Mr.Vikas patel",
      businessOwner: "DAMS Docter",
    },
    {
      id: 5,
      image: "Banner-5.jpg",
      feedbackerImage: "Banner-1.jpg",
      stars: "",
      feedbackText: "Pixonate Lab consistently delivers top-notch solutions. Highly recommended for tech projects.",
      feedbackerName: "Mr.Vivek patil",
      businessOwner: "Docter",
    },
  ];

  return (
    <div className="feedback-container">
      <div className="top-section">
        <p>Client Feedback</p>
        <h1>We provide our clients with the most innovative and effective solutions</h1>
      </div>

      <Carousel indicators={false} interval={2000}>
        {feedbackItems.map((item) => (
          <Carousel.Item key={item.id}>
            <img
              className="d-block w-100 carousel-image"
              src={item.image}
              alt={`Client Feedback ${item.id}`}
            />
            <Carousel.Caption>
              <div className="feedback-content">
                <div className="stars">{item.stars}</div>
                <p>{item.feedbackText}</p>
                <div className="feedbacker">
                  <img src={item.feedbackerImage} alt="Feedbacker" />
                  <div>
                    <p>{item.feedbackerName}</p>
                    <p>{item.businessOwner}</p>
                  </div>
                </div>
              </div>
            </Carousel.Caption>
          </Carousel.Item>
        ))}
      </Carousel>
    </div>
  );
};

export default ClientFeedback;
