import React from "react";
import "../Style/Home.css";
import { Link } from "react-router-dom";
import ReactPlayer from "react-player";
import Aabout from "./Aabout";
import Who from "./Who";
import What from "./What";
import Banner from "./Banner";
import Pricing from "./Pricing";
import Take from "./Take";
import Support from "./Support";
import Client from "./Client";
import Blog from "./Blog";
import Expert1 from "./Expert1";

const Home = () => {
  return (
    <>
      <div className="home-container">
        <ReactPlayer
          url="/video2.mp4"
          playing
          loop
          muted
          width="100%"
          height="100vh"
        />

        <div className="home-content">
          <div className="home-text">
            <p>CUSTOM SOFTWARE DEVELOPMENT</p>
            <h1>
              Specialize In Providing
              <br />
              Custom Software
              <br /> Solutions Unique Needs
            </h1>
            <p>
              At Pixonate, we go beyond helping businesses transform through
              technology. We help them make a meaningful difference; to their
              customers, and to the communities they serve.{" "}
            </p>
          </div>

          <div className="home-buttons">
            <div className="button-row">
              <Link to="/get-started" className="btn btn-primary">
                Get Started
              </Link>
            </div>
            <div className="button-row">
              <Link to="/see-product" className="btn btn-secondary">
                See Product
              </Link>
            </div>
          </div>
        </div>
      </div>
      <Aabout />
      <Who />
      <What />
      <Expert1 />
      <Take />
      <Pricing />
      <Banner />

      <Support />
      <Client />
      <Blog />
    </>
  );
};

export default Home;
