import React from "react";
import "../Style/Banner.css";

const Banner = () => {
  return (
    <div className="banner-container">
      <div className="banner-content">
        <p>Bring Your Ideas</p>
        <h1 className="Banner">
          We Have Experts to Build Customized Software from Ideas
        </h1>
        <p>
          "EcoTech Innovations leads the way in sustainable tech solutions,
          crafting innovations that merge environmental consciousness with
          cutting-edge technology for a greener, brighter future."
        </p>
        <button className="btn">Get Started</button>
      </div>
    </div>
  );
};

export default Banner;
